import { defineStore } from 'pinia'
import { inject } from 'vue'
export default defineStore({
  id: 'Article',
  state: () => {
    return {
      NowNum:1,
      ArticleCount:0,
      ArticleGrop:[],
      axios:inject('$axios'),
      selectArticle:[]
    }  
  },
  
  getters: {
  },
  actions: {
    async get_Article(page_size:number=1){
      let resultValue = await this.axios.get("/send/get_articles",{
        params:{page:page_size}
      })
      this.$patch(state => {
        state.ArticleGrop = resultValue.results?.msg
        state.ArticleCount = resultValue.count
        state.NowNum = page_size
      })
    },
    async get_SelectArticle(query:string){
      const resultValue = await this.axios.get(`/send/select_articles`, {
        params: {q:query},
      })
      this.$patch(state => {
        state.selectArticle = resultValue
      })
    }
  },
  persist: {
    enabled: true,
    strategies: [
      {
        storage: sessionStorage,
        paths: ['NowNum','ArticleCount','ArticleGrop'],
      }
    ]
  }
})